.desctop-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 61px 0;
  z-index: 999;
}

.navigation {
  display: flex;
  gap: 60px;
}

.navigation__item {
  font-size: 14px;
  line-height: 24px;
  color: #505F98;
  transition: border .8s ease;
}

.navigation__item:hover {
  border-bottom: 1px solid;
}

.logo {
  font-family: 'Roboto-Black';
  font-weight: 900;
  font-size: 26px;
  line-height: 38px;
  color: #37447E;
  transition: all .5s ease;
  margin-right: 80px;
}

.logo:hover {
  color: #576ed4;
}

@media (max-width: 929.98px) {
  .logo {
    margin-right: 0;
  }
}

.menu-button {
  width: 160px;
  background: #111B47;
  border-radius: 2px;
  padding: 4px 0;
  margin-right: 8px;
  text-align: center;
  transition: all .5s ease;
}

.menu-button:hover {
  background: #576ed4;
}

.menu-button__link {
  font-family: 'Roboto-Medium';
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
}

@media (max-width: 929.98px) {
  .desctop-menu {
    padding: 30px 0;
  }
}

.header__humburger {
  display: none;
}

@media (max-width: 929.98px) {
  .header__humburger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
  }
  .header__humburger::after, .header__humburger::before,
  .header__humburger span {
    position: absolute;
    left: 0;
    content: '';
    width: 30px;
    height: 2px;
    background: #091133;
    transition: .5s ease;
  }
  .header__humburger span {
    top: 9px;
  }
  .header__humburger::after {
    bottom: 0;
  }
  .header__humburger::before {
    top: 0;
  }
  .header__humburger.active::before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header__humburger.active::after {
    transform: rotate(-45deg);
    top: 9px;
  }
  .header__humburger.active span {
    transform: scale(0);
  }
}

@media (max-width: 929.98px) {
  body.lock {
    overflow: hidden;
  }
  .navigation {
    display: block;
    position: fixed;
    top: 100px;
    left: -100%;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: rgba(255, 255, 255, 0.9);
    padding: 20px 0;
    transition: all .5s ease-in-out;
    text-align: center;
    overflow: auto;
  }
  .navigation .header__item {
    margin-top: 20px;
  }
  .navigation.active {
    left: 0%;
  }
}

